  gap: 0.75rem;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #e5e7eb;
}
.modal-body .info-section .supplier-info .supplier-card .supplier-header svg {
  color: #6b7280;
}
.modal-body .info-section .supplier-info .supplier-card .supplier-header .supplier-details .supplier-name {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
