}
.modal-body .info-section .supplier-info .supplier-card .supplier-header .supplier-details .supplier-id {
  font-size: 0.875rem;
  color: #6b7280;
  font-family: "Courier New", monospace;
}
.modal-body .info-section .supplier-info .supplier-card .supplier-contact {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
