}
.modal-body .info-section .supplier-info .supplier-card .supplier-contact .contact-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.modal-body .info-section .supplier-info .supplier-card .supplier-contact .contact-item label {
  font-size: 0.75rem;
  font-weight: 500;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.modal-body .info-section .supplier-info .supplier-card .supplier-contact .contact-item span {
  font-size: 0.875rem;
  color: #111827;
}
.modal-body .info-section .items-table-container {
  overflow-x: auto;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
