}
.modal-body .info-section .items-table-container .items-table thead th {
  padding: 0.75rem;
  text-align: left;
  font-weight: 600;
  color: #374151;
  white-space: nowrap;
  border-bottom: 1px solid #e5e7eb;
}
.modal-body .info-section .items-table-container .items-table tbody tr {
  border-bottom: 1px solid #f3f4f6;
}
.modal-body .info-section .items-table-container .items-table tbody tr:last-child {
