  }
  .modal-body .info-section .info-grid {
    grid-template-columns: 1fr;
  }
  .modal-body .info-section .supplier-info .supplier-card .supplier-contact {
    grid-template-columns: 1fr;
  }
  .modal-body .info-section .items-table-container .items-table {
