  }
  .modal-body .info-section .items-table-container .items-table thead th,
  .modal-body .info-section .items-table-container .items-table tbody td {
    padding: 0.5rem;
  }
  .modal-body .info-section .additional-info {
    grid-template-columns: 1fr;
  }
  .modal-footer {
    padding: 1rem;
    flex-direction: column;
  }
  .modal-footer .btn {
    width: 100%;
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .modal-overlay {
    padding: 0.5rem;
  }
  .modal-content {
    max-width: 100vw;
    margin: 0;
    border-radius: 0;
    max-height: 100vh;
  }
  .modal-header {
    border-radius: 0;
  }
  .modal-footer {
    border-radius: 0;
  }
}
